import React from 'react';
import { Route, Switch } from 'react-router-dom';
import CommonListing from 'AppComponents/Apis/Listing/CommonListing';
import Details from 'AppComponents/Apis/Details/index';
import { ResourceNotFound } from 'AppComponents/Base/Errors';

const MCPServers = () => {
    return (
        <Switch>
            <Route
                exact
                path='/mcp-servers'
                render={(props) => (
                    <CommonListing {...props} />)}
            />
            <Route
                path='/mcp-servers/:serverUuid/'
                render={(props) => (
                    <Details {...props} />)}
            />
            <Route component={ResourceNotFound} />
        </Switch>
    );
};

export default MCPServers;
